        flex-flow: column nowrap;
        width: 100%;
        height: auto;


    }

}


.stats-div {
    display: flex;
    flex-flow: row nowrap;
    grid-area: stats;
    justify-content: space-evenly;
    width: 100%;

    align-items: center;
    padding-bottom: 80px;
